body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: blueviolet;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
  margin-top: 8%;
  margin-bottom: 5%;
  text-align: center;
  text-transform: capitalize;
  font-size: 20px;
}
.underline{
  width:5%;
  border: 3px solid rgb(49, 217, 223);
  margin: auto;
}
.quote-icon{
  color: rgb(49, 217, 223);
  font-size: 50px;
  float: left;
  margin-left: 40%;
  margin-top: 10%;
}
.review-container{
  text-align: center;
  background-color: rgb(172, 163, 112);
  width: 500px;
  margin: auto;
}
img{
  float: right;
  margin-right: 20px;
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 20px;
}
a{
  text-decoration: none;
  color: rgb(123, 43, 148);
  margin-bottom: 20px;
  font-size: 20px;
}
p{
  display: inline-block;
  margin: 20px 20px 20px 20px;
}
.btn1{
  border: none;
  background-color: rgb(172, 163, 112);
  color: rgb(49, 217, 223);
  font-size: 30px;
  margin-bottom: 10px;
}
.random-btn{
  border: 2px solid black;
  padding: 5px 5px;
  font-size: 20px;
  letter-spacing: 1px;
  margin-bottom: 15px;
  border-radius: 5px;
  color: rgb(123, 43, 148);
  background-color: rgb(49, 217, 223);
}